:root {
  --primary: #4f46e5;
  --primary-dark: #4338ca;
  --primary-light: #818cf8;
  --text-white: #fff;
  --text-muted: #6b7280;
  --bg-white: #fff;
  --shadow-sm: 0 1px 2px 0 #0000000d;
  --shadow-md: 0 4px 6px -1px #0000001a, 0 2px 4px -1px #0000000f;
  --shadow-lg: 0 10px 15px -3px #0000001a, 0 4px 6px -2px #0000000d;
}

body {
  font-family: Poppins, sans-serif;
  line-height: 1.6;
}

.navbar {
  background: var(--bg-white);
  box-shadow: var(--shadow-sm);
  z-index: 1000;
  padding: 1rem 0;
  position: sticky;
  top: 0;
}

.navbar-brand {
  color: var(--primary);
  align-items: center;
  gap: .75rem;
  font-weight: 600;
  display: flex;
}

.navbar-brand img {
  width: 32px;
  height: 32px;
}

.nav-link {
  font-weight: 500;
  transition: color .2s;
  color: var(--text-muted) !important;
}

.nav-link:hover {
  color: var(--primary) !important;
}

.nav-link.active {
  font-weight: 600;
  color: var(--primary) !important;
}

.version-tag {
  background: var(--primary-light);
  color: var(--text-white);
  border-radius: 9999px;
  margin-left: .5rem;
  padding: .25rem .5rem;
  font-size: .75rem;
  font-weight: 500;
}

.hero {
  z-index: 1;
  background: linear-gradient(135deg, #4338ca 0%, #6366f1 30%, #7c3aed 60%, #8b5cf6 80%, #a78bfa 100%);
  padding: 10rem 0 14rem;
  position: relative;
  overflow: hidden;
}

.hero:before {
  content: "";
  z-index: 0;
  background: radial-gradient(circle at 100% 0, #c084fc4d 0%, #8b5cf633 30%, #7c3aed1a 60%, #0000 80%);
  position: absolute;
  inset: 0;
}

.hero .container {
  z-index: 2;
  position: relative;
}

.hero h1 {
  color: #fff;
  text-shadow: 0 2px 4px #00000026;
  margin-bottom: 1.5rem;
  font-size: 3.5rem;
  font-weight: 800;
  line-height: 1.2;
}

.hero .lead {
  color: #fff;
  opacity: .95;
  margin-bottom: 2rem;
  font-size: 1.25rem;
  font-weight: 400;
}

.btn-light {
  color: var(--primary);
  background: #fff;
  border: none;
  border-radius: 8px;
  padding: .75rem 2rem;
  font-weight: 600;
  transition: transform .2s, box-shadow .2s;
}

.btn-light:hover {
  color: var(--primary);
  box-shadow: var(--shadow-lg);
  background: #fff;
  transform: translateY(-2px);
}

.btn-outline-light {
  color: #fff;
  border: 2px solid #fff;
  border-radius: 8px;
  padding: .75rem 2rem;
  font-weight: 600;
  transition: transform .2s, background .2s;
}

.btn-outline-light:hover {
  color: #fff;
  background: #ffffff1a;
  border-color: #fff;
  transform: translateY(-2px);
}

.preview-window {
  transform-style: preserve-3d;
  perspective: 1000px;
  backdrop-filter: blur(10px);
  background: none;
  border-radius: 1rem;
  max-width: 900px;
  margin: 0 auto;
  transition: transform .3s, box-shadow .3s;
  overflow: hidden;
  transform: rotateY(-2deg)rotateX(2deg);
  box-shadow: 0 20px 40px -5px #00000026, 0 10px 20px -6px #0000001a;
}

.preview-window:hover {
  transform: rotateY(0)rotateX(0);
  box-shadow: 0 25px 50px -12px #00000040;
}

.preview-header {
  background: #ffffff1a;
  border-bottom: 1px solid #ffffff1a;
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
  padding: .75rem;
}

.preview-dots {
  gap: .5rem;
  display: flex;
}

.preview-dots span {
  opacity: .9;
  border-radius: 50%;
  width: 12px;
  height: 12px;
}

.preview-dots span:first-child {
  background: #ef4444;
}

.preview-dots span:nth-child(2) {
  background: #f59e0b;
}

.preview-dots span:nth-child(3) {
  background: #10b981;
}

.preview-content {
  background: #ffffff0d;
  grid-template-columns: minmax(0, 1fr) minmax(0, 1fr);
  height: 400px;
  display: grid;
}

.preview-input {
  background: #00000080;
  border-right: 1px solid #ffffff1a;
  padding: 1.25rem;
  overflow: auto;
}

.preview-input pre {
  margin: 0;
  padding: 0;
  font-size: .7rem;
  background: none !important;
}

.preview-input code {
  color: #e5e7eb;
  font-family: Fira Code, monospace;
  font-size: .7rem;
  line-height: 1.4;
}

.preview-output {
  background: #fffffff2;
  justify-content: center;
  align-items: flex-start;
  padding: .75rem;
  display: flex;
  position: relative;
  overflow: hidden;
}

.preview-split {
  display: contents;
}

.preview-output .classic-university-theme {
  transform-origin: top;
  background: #fff;
  width: 100%;
  margin: -1rem -.5rem;
  position: relative;
  transform: scale(.75);
}

.preview-output .credential-banner {
  background: linear-gradient(135deg, var(--primary-dark), var(--primary));
  z-index: 1;
  text-align: center;
  padding: 1.25rem;
  position: relative;
}

.preview-output .credential-banner h1 {
  color: #fff;
  text-shadow: none;
  margin-bottom: .5rem;
  font-size: 1.75rem;
}

.preview-output .university-name {
  color: #fff;
  opacity: .9;
  font-size: 1.1rem;
}

.preview-output .degree-content {
  z-index: 2;
  background: #fff;
  padding: 1rem;
  position: relative;
}

.preview-output .content-card {
  background: #fff;
  border-radius: .75rem;
  padding: 1rem;
  box-shadow: 0 4px 6px -1px #0000001a, 0 2px 4px -1px #0000000f;
}

.preview-output .degree-header {
  text-align: center;
  margin-bottom: 1rem;
}

.preview-output .degree-name {
  margin-bottom: .25rem;
  font-size: 1.5rem;
  font-weight: 600;
}

.preview-output .degree-type {
  color: var(--text-muted);
  font-size: 1.1rem;
}

.preview-output .info-grid {
  grid-template-columns: 1fr;
  gap: .75rem;
  margin: 0;
  display: grid;
}

.preview-output .info-item {
  background: #f9fafb;
  border-radius: .5rem;
  margin: 0;
  padding: .75rem;
}

.preview-output .info-label {
  color: var(--text-muted);
  text-transform: uppercase;
  letter-spacing: .025em;
  margin-bottom: .25rem;
  font-size: .875rem;
  font-weight: 600;
}

.preview-output .info-value {
  color: var(--text-dark);
  font-size: 1rem;
  font-weight: 500;
}

.credential-preview {
  text-align: center;
  background: #fff;
  border-radius: .5rem;
  padding: 2rem;
  box-shadow: 0 1px 3px #0000001a;
}

.credential-header {
  margin-bottom: 2rem;
}

.credential-logo {
  width: 64px;
  height: 64px;
  margin-bottom: 1rem;
}

.credential-header h2 {
  color: var(--primary);
  margin: 0;
  font-size: 1.5rem;
}

.credential-body {
  color: #1f2937;
}

.credential-body h3 {
  margin-bottom: .5rem;
  font-size: 1.75rem;
}

.credential-body p {
  color: #4b5563;
  margin-bottom: 2rem;
}

.credential-recipient {
  margin-top: 3rem;
}

.credential-recipient h4 {
  color: #1f2937;
  margin: 1rem 0;
  font-size: 1.5rem;
}

.credential-recipient p {
  color: #6b7280;
  margin-bottom: .5rem;
}

.features {
  background: var(--bg-white);
  z-index: 2;
  margin-top: -8rem;
  padding: 6rem 0;
  position: relative;
}

.features:before {
  content: "";
  background: linear-gradient(to bottom, transparent 0%, #fff3 30%, #ffffff80 50%, #fffc 70%, var(--bg-white) 100%);
  pointer-events: none;
  height: 200px;
  position: absolute;
  top: -200px;
  left: 0;
  right: 0;
}

.feature-card {
  text-align: center;
  background: #fff;
  border-radius: 1rem;
  flex-direction: column;
  align-items: center;
  max-width: 320px;
  height: 100%;
  margin: 0 auto;
  padding: 1.25rem;
  transition: transform .2s, box-shadow .2s;
  display: flex;
  box-shadow: 0 1px 3px #0000001a;
}

.feature-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 4px 6px -1px #0000001a, 0 2px 4px -1px #0000000f;
}

.feature-icon {
  color: #4f46e5;
  margin-bottom: 1rem;
  font-size: 3.5rem;
}

.feature-card h3 {
  margin-bottom: .5rem;
  font-size: 1.125rem;
  font-weight: 600;
}

.feature-card p {
  margin-bottom: 1rem;
  font-size: .875rem;
}

.feature-card .btn {
  margin-top: auto;
  padding: .5rem 1.25rem;
}

.footer {
  background: linear-gradient(135deg, var(--primary) 0%, var(--primary-light) 100%);
  color: var(--text-white);
  padding: 4rem 0;
}

.footer h5 {
  font-weight: 600;
}

.footer a {
  text-decoration: none;
  transition: opacity .2s;
}

.footer a:hover {
  opacity: 1 !important;
}

@media (width <= 991.98px) {
  .hero {
    padding: 8rem 0 6rem;
  }

  .preview-container {
    margin-top: 3rem;
  }

  .preview-window {
    transform: none;
  }

  .preview-content {
    grid-template-columns: 1fr;
    height: auto;
    max-height: 700px;
  }

  .preview-input {
    height: 200px;
  }

  .preview-output {
    height: 300px;
  }

  .preview-output .classic-university-theme {
    margin: -2rem -1rem;
    transform: scale(.6);
  }

  .footer .d-flex {
    margin-top: 2rem;
    justify-content: flex-start !important;
  }
}

@media (width <= 767.98px) {
  .hero h1 {
    font-size: 2.5rem;
  }

  .features {
    padding: 4rem 0;
  }
}

.getting-started {
  background: var(--bg-white);
  padding: 4rem 0;
  position: relative;
}

.getting-started h2 {
  color: var(--primary);
  background: linear-gradient(135deg, var(--primary-dark), var(--primary));
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  margin-bottom: 1rem;
  font-size: 2.5rem;
  font-weight: 700;
}

.getting-started .lead {
  color: var(--text-muted);
  margin-bottom: 2rem;
  font-size: 1.1rem;
}

.getting-started-steps {
  counter-reset: step;
  margin: 0 0 2rem;
  padding: 0;
  list-style: none;
}

.getting-started-steps li {
  counter-increment: step;
  color: var(--text-muted);
  margin-bottom: 1rem;
  padding-left: 3rem;
  font-size: 1.1rem;
  position: relative;
}

.getting-started-steps li:before {
  content: counter(step);
  background: linear-gradient(135deg, var(--primary-dark), var(--primary));
  color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 2rem;
  height: 2rem;
  font-size: .9rem;
  font-weight: 600;
  display: flex;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.editor-preview {
  background: #1e1e1e;
  border-radius: .75rem;
  width: 100%;
  max-width: 500px;
  margin-left: auto;
  overflow: hidden;
  box-shadow: 0 4px 15px #0000001a;
}

.editor-header {
  background: #2d2d2d;
  border-bottom: 1px solid #ffffff1a;
  padding: .75rem 1.25rem;
}

.editor-header span {
  color: #ffffffb3;
  font-size: .875rem;
  font-weight: 500;
}

.editor-content {
  height: 180px;
  margin: 0;
  overflow: auto;
  background: none !important;
  padding: 1.25rem !important;
}

.editor-content code {
  color: #e5e7eb;
  font-family: Fira Code, monospace;
  font-size: .75rem;
  line-height: 1.5;
  background: none !important;
}

.token.property, .token.key {
  color: #9cdcfe !important;
}

.token.string {
  color: #ce9178 !important;
}

.token.punctuation {
  color: #d4d4d4 !important;
}

@media (width <= 991.98px) {
  .editor-preview {
    margin: 2rem auto 0;
  }
}

.section-padding {
  padding: 4rem 0;
}

.themes-section .card.bg-light {
  box-shadow: var(--shadow-sm);
  border-radius: 1rem;
}
